@import "src/breakpoints.scss";

.FeaturedProjectCard_container {
  background-color: black;
  margin: 25px 0px;
  display: flex;
  flex-direction: row;

  @media (max-width: $breakpoint-tablet) {
    flex-direction: column-reverse;
    max-width: 400px;

    .project_details {
      padding: 20px 20px 40px !important;

      .title_section {
        margin-bottom: 10px;
      }
    }

    img {
      border-top-right-radius: 15px !important;
      border-top-left-radius: 15px;
      border-bottom-right-radius: 0px !important;
      width: 100%;
      max-height: 300px;
    }
  }

  .project_details {
    padding: 40px 40px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    .title_section {
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      align-items: center;

      .links {
        font-size: 25px;
        svg {
          cursor: pointer;
          color: var(--blue);

          &:last-child {
            margin-right: 5px;
          }
        }
      }
    }

    .description {
      color: white;
      margin-bottom: 20px;
    }

    .technologies {
      margin-right: 10px;
    }
  }
  .project_image {
    img {
      max-width: 400px;
      border-top-right-radius: 20px;
      border-bottom-right-radius: 20px;
    }
  }
}
